<template>
    <home-container title="Devices" default-href="/home/account/security" :with-header="step !== 1 ? false : true">
        <div v-if="step === 1">
            <ion-text><strong>Nokia 2.3</strong></ion-text>
            
            <ion-list class='ion-margin-vertical'>
                <ion-item lines='full' class='mb-2'>
                    <ion-label slot="start"><span class='text-muted small'>Location</span></ion-label>
                    <ion-label slot="end">Lagos</ion-label>
                </ion-item>
                <ion-item lines='full' class='mb-2'>
                    <ion-label slot="start"><span class='text-muted small'>IP address</span></ion-label>
                    <ion-label slot="end">192.678.098.121</ion-label>
                </ion-item>
                <ion-item lines='full' class='mb-2'>
                    <ion-label slot="start"><span class='text-muted small'>Time</span></ion-label>
                    <ion-label slot="end">19:30 AM</ion-label>
                </ion-item>
                <ion-item lines='full' class='mb-2'>
                    <ion-label slot="start"><span class='text-muted small'>Date</span></ion-label>
                    <ion-label slot="end">23-03-2020</ion-label>
                </ion-item>
            </ion-list>
            <div class='ion-margin d-flex flex-column'>
                <h6 class='mb-2'>New device detected</h6>
                <ion-text><strong>Samsung S5</strong></ion-text>
                <ion-text><span class='my-2'>Lagos</span></ion-text>
                <ion-text><span class='my-2'>12:30PM <ion-icon class='small' :icon='ellipse'></ion-icon> 12-02-2021</span></ion-text>
                <ion-text><span class='my-2'>125.678.789.089</span></ion-text>
                <ion-text><p class='my-2'>Do you recognise this activity?</p></ion-text>
                <div class='d-flex'>
                    <ion-button strong @click.prevent="nextStep" style='margin-right:1rem' >Trust</ion-button>
                    <ion-button strong>Block</ion-button>
                </div>

            </div>
        </div>
        <div v-if="step === 2">
            <ion-item lines="none" class="ion-no-padding">
                <ion-button color='dark' slot="start" fill="clear" @click.prevent="step--">
                <ion-icon slot="icon-only" :icon="arrowBackOutline"></ion-icon>
                </ion-button>
                <ion-text slot="start"><h1 class="">Verification</h1></ion-text>
            </ion-item>
            <div class="row my-3">
                <div class="col-9 mx-auto text-center">
                    <ion-text><p class="font-weight-light">Enter 4-digit verification,<br />sent to your email address</p></ion-text>
                </div>
            </div>
            <div class="d-flex justify-content-center">
                <v-otp-input input-type="password"  @on-complete="handleOnComplete" inputClasses="otp-input" :numInputs="4" separator=" "></v-otp-input>
            </div>
            <div class="row my-4">
                <div class="col-9 mx-auto text-center">
                <ion-text
                    ><p class="">
                    Code Expires in,<br />
                    <strong>{{ countdown }}</strong>
                    </p></ion-text
                >
                </div>
            </div>
            <ion-button strong :disabled="!authorized" @click.prevent="processSend" class="ion-margin-horizontal my-4" expand="block">
                <ion-label>Authorize</ion-label>
            </ion-button>
            <div class="my-4">
                <ion-text>
                    <p class="d-flex flex-column align-items-end justify-content-end">
                        <ion-text>Didn't get the code?</ion-text>
                            <strong @click="countDownTick" class="text-primary">Request another code</strong >
                    </p>
                </ion-text
                >
            </div>
        </div>
        <div v-if="step === 3" style="height: 100vh" class="d-flex flex-column align-items-center justify-content-center" >
            <ion-icon class="icon-container mb-5" style="font-size: 4rem" :icon="checkmarkOutline" ></ion-icon>
            <p class='lead text-center'>New device <strong>Samsung S5 </strong> has been <br> added successfully</p>
            <ion-button strong @click.prevent="router.push('/home/account/security')"  class="ion-margin-horizontal my-4 w-100" expand="block">
                <ion-label>Home</ion-label>
            </ion-button>
        </div>
    </home-container>
    
</template>
<script>
import { defineComponent } from "vue";
import {ellipse,checkmarkOutline,arrowBackOutline} from "ionicons/icons";
import { useRouter } from "vue-router";
import { loadingController } from "@ionic/vue";
import VOtpInput from 'vue3-otp-input';

    export default defineComponent({
        components: { VOtpInput },
        data() {
            return {
                step: 1,
                countdown: null,
                timer: null,
                disableAuthorization: true,
                otp:null,
            }
        },
        async ionViewDidLeave() {
            this.resetFields();
        },
        computed:{
            authorized(){
                return this.otp && !this.disableAuthorization
            }
        },
        setup() {
            const router = useRouter();
            return {
                router,ellipse,checkmarkOutline,arrowBackOutline
            };
        },
        methods: {
            resetFields() {
                clearInterval(this.timer);
                this.step = 1;
                this.countdown = null;
                this.timer = null;
                this.disableAuthorization = true;
                this.otp = null;
            },
            countDownTick() {
                clearInterval(this.timer);
                this.disableAuthorization = false;
                let countDownDate = new Date().setMinutes(new Date().getMinutes() + 1);
                this.timer = setInterval(() => {
                    var now = new Date().getTime();
                    var distance = countDownDate - now;
                    var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
                    var seconds = Math.floor((distance % (1000 * 60)) / 1000);
                    minutes = minutes < 10 ? "0" + minutes : minutes;
                    seconds = seconds < 10 ? "0" + seconds : seconds;
                    if (distance < 2) {
                    clearInterval(this.timer);
                    this.disableAuthorization = true;
                    return;
                    }
                    this.countdown = minutes + ":" + seconds;
                }, 1000);
            },
            handleOnComplete($event){
                this.otp = $event;
                this.disableAuthorization = false;
                this.processSend();
            },
            async processSend(){
                let loading = await loadingController.create({
                    message: "Please wait...",
                });
                loading.present();
                setTimeout(() => {
                    this.step++;
                    loading.dismiss();
                }, 1000);
            },
            nextStep() {
                this.step++;
                clearInterval(this.timer);
                if (this.step === 2) {
                    this.countDownTick();
                }
            },
        }
});

</script>
